<template>
  <div class="clock-box wrap-shadow">
    <template v-if="specialDay">
      <div
        :style="{backgroundImage: `url(${imgBg}?v=1)`}"
        class="dom2img-box"
        ref="clockImg">
        <img class="morning-img" v-if="morningImg" :src="morningImg" alt="">
        <!-- 特殊日期-->
        <template v-if="specialDay">
          <div class="top-box">
            <div class="clock-info">
              <div class="img-box flex">
                <img :src="userInfo.headImg" alt="">
              </div>
              <div class="clock-detail flex-column">
                <span>{{minutes}}</span>
                <span>{{`连续早起${pinchinInfo.continueNum}天`}}</span>
                <span>{{date}}</span>
              </div>
            </div>
          </div>
        </template>
        <template v-else>
          <div class="top-box">
            <img class="morning-text" src="../imgs/morning.png"/>
            <div class="clock-info">
              <div class="img-box flex">
                <img :src="userInfo.headImg" alt="">
              </div>
              <div class="clock-detail flex-column">
                <span>{{minutes}}</span>
                <span>{{`连续早起${pinchinInfo.continueNum}天`}}</span>
                <span>{{date}}</span>
              </div>
            </div>
          </div>
          <div class="bottom-box flex">
            <div class="bottom-left flex">
              <img src="../imgs/qr-code.jpg" alt="">
              <span>扫码互道早安</span>
            </div>
            <div class="bottom-right flex">
              <span>{{pinchinInfo.punchinTips || '尚未佩妥剑，转眼便江湖。 愿历尽千帆，归来仍少年。'}}</span>
            </div>
          </div>
        </template>
      </div>
    </template>

    <template v-else>
      <div
        :style="{backgroundImage: `url(${imgBg}?v=1)`}"
        class="dom2img-box-new"
        ref="clockImg">
       <img class="morning-img" v-if="morningImg" :src="morningImg" alt="">

        <div class="cont-box">
          <img class="img-block" src="../imgs/new-block.png" alt="">

          <div class="top-box-new">
            <div class="new-headimg">
              <div class="img-box-new flex">
                <img :src="userInfo.headImg" alt="">
              </div>
              <div class="clock-detail flex-column">
                <span>{{minutes}}</span>
                <span>{{date}}</span>
              </div>
            </div>
            <span class="continue-num">{{`连续早起${pinchinInfo.continueNum}天`}}</span>
          </div>

          <div class="text-cont">
            <span class="text-item"
                  :style="{lineHeight: (item === '，' || item === '。') ?  '0.1rem': '0.4533rem'}"
                  v-for="(item, index) in punchinTips" :key="index">{{item}}</span>
          </div>

          <div class="qr-name">好物好生活</div>
        </div>

      </div>

    </template>
    <div v-if="morningImg" class="text-tip">长按图片保存到手机</div>
  </div>
</template>

<script>
  import html2canvas from 'html2canvas';

  import {mapState} from 'vuex';
  import {getUrlParam, parseTime} from "../../../utils/tools";
  import {baseUrl} from '../../../utils/request'
  export default {
    name: "morning-clock",
    props: {
      pinchinInfo: {
        type: Object,
        default() {
          return {}
        }
      }
    },
    data() {
      return {
        morningImg: '',
        morningImgURL: baseUrl.morningImgURL,
        defaultImg: require('../imgs/bg-morning.jpg'),
      }
    },
    computed: {
      ...mapState([
        'userInfo',
      ]),
      minutes() {
        const hourM = this.pinchinInfo.punchinTime.split(' ')[1]
        const flag = hourM.indexOf(':', 3)
        return hourM.substring(0, flag)
      },
      date() {
        return parseTime(new Date(this.pinchinInfo.punchinTime.split(' ')[0]), '{yyyy}/{mm}/{dd}')
      },
      imgBg() {
        const punchinBgImg = this.pinchinInfo.punchinBgImg
        return punchinBgImg ? this.morningImgURL + punchinBgImg : this.defaultImg
      },
      specialDay() {
        return this.pinchinInfo.punchinBgImg ? this.pinchinInfo.punchinBgImg.length > 10 : false
      },
      punchinTips() {
        return this.pinchinInfo.punchinTips.split('').filter(item => item)
      }
    },
    mounted() {
      this.timer = setTimeout(() => {
        const dom = this.$refs.clockImg
        //
        html2canvas(dom,{useCORS: true, backgroundColor: 'rgba(255,255,255,0)'}).then(canvas => {
          this.morningImg = canvas.toDataURL("image/png")
        })
        // domtoimage.toPng(dom,{useCORS: true })
        //   .then(dataUrl => {
        //     this.morningImg = dataUrl;
        //   })
        //   .catch(function (error) {
        //     console.error('oops, something went wrong!', error);
        //   });
      },100)
    },
    destroyed() {
      clearTimeout(this.timer)
    }
  }
</script>

